import React from 'react';
import ReactDOM from 'react-dom';
import SocialMedia from './socialMedia';
import emailjs from 'emailjs-com';

const style = {
	title:{
		textAlign: 'center',
		color: 'black',
  		fontFamily:'Raleway, sans-serif',
  		fontWeight: '600',
  		letterSpacing: '5px',
  		margin: '35px',
  		fontSize: 'x-large'
  },
  inputBox:{
  	width: '60%',
    height: '150px',
    padding: '10px 12px',
    boxSizing: 'border-box',
    border: '2px solid #ccc',
    borderRadius: '4px',
    backgroundColor: 'rgb(243, 243, 243)',
    fontSize: '14px',
    resize: 'none',
    border: '2px solid black',
    fontFamily:'Raleway, sans-serif',
  	fontWeight: 'normal',
  	letterSpacing: '1px'
  },
  text:{
  	fontFamily:'Raleway, sans-serif',
  	fontWeight: '600',
  	letterSpacing: '5px'
  },
  nameMailBox:{
  	textAlign: 'center',
    width: '-webkit-fill-available',
    boxSizing: 'border-box',
    border: '2px solid black',
    borderRadius: '4px',
    backgroundColor: 'rgb(243, 243, 243)',
    fontSize: '14px',
    resize: 'none',
    fontFamily: 'Raleway, sans-serif',
    letterSpacing: '3px',
    fontWeight: 'normal',
    marginBottom: '10px',
    padding: '5px'
  }
}



class About extends React.Component {


  render() {

  	function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('celeste_denadai_gmail_com', 'template_l6VmPlu5', e.target, 'user_SRHNZ1pOtA2q32NMfgn6Z')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  }

    return (
    	<div style={{ paddingTop: '55px' }}>
    		<div style={style.title}>CONTACT</div>
    		<form style={{ textAlign: 'center' }} onSubmit={sendEmail}>
        		<p style={style.text}>Leave a messagge</p>

        		
        		<div style={{  margin: '0 auto' , width: '60%' }}>
        			{/*<label style={style.text}>Name:</label>*/}
      				<input placeholder="Name" type="text" name="senderName" style={style.nameMailBox}/>
      			{/*</div>

      			
      			<div>*/}
      				{/*<label style={style.text}>Email:</label>*/}
      				<input type="email" name="senderMail" placeholder="E-Mail" style={style.nameMailBox}/>
      			</div>

        		<textarea name="message_html" style={style.inputBox} placeholder="Leave a messagge..."/>

        		<div style={{ marginTop: '30px' }}>
        			<input type="submit" value="Send" className='submit' />
        		</div>
      		</form>

      		<div style={{ marginTop: '30px' }}>
				<SocialMedia />
			</div>
			
		</div>
    );
  }
}


export default About;



