import React from 'react';
import ReactDOM from 'react-dom';

const facebook = "https://www.facebook.com/Franco-De-Nadai-2957635120932123"
const instagram = "https://www.instagram.com/franco_denadai/"

class SocialMedia extends React.Component {

	goToFacebook() {
    	window.open(facebook);
  	}
  	goToInsta() {
    	window.open(instagram);
  	}

  render() {
    return (
    	<div className="contenitorIcons">
		    <img className="icon" src="https://img.icons8.com/ios-filled/50/000000/instagram-new.png" onClick={this.goToInsta}/>
		    <img className="icon" src="https://img.icons8.com/ios-filled/50/000000/facebook-new.png" onClick={this.goToFacebook}/>
		    <img className="icon" src="https://img.icons8.com/ios-filled/50/000000/email-sign.png"/>
	    </div>
    );
  }
}


export default SocialMedia;

