import React, { useState } from 'react';
import styled from 'styled-components';
import { Link } from "react-router-dom";

const StyledBurger = styled.div`
  width: 2rem;
  height: 2rem;
  position: fixed;
  top: 12px;
  right: 20px;
  z-index: 20;
  display: none;
  color: #000;
  @media (max-width: 850px) {
    display: flex;
    justify-content: space-around;
    flex-flow: column nowrap;
    color: #000;
  }
  div {
    top: 12px;
    width: 2rem;
    height: 0.25rem;
    background-color: ${({ open }) => open ? '#ccc' : '#333'};
    border-radius: 10px;
    transform-origin: 1px;
    transition: all 0.3s linear;
    &:nth-child(1) {
      transform: ${({ open }) => open ? 'rotate(45deg)' : 'rotate(0)'};
    }
    &:nth-child(2) {
      transform: ${({ open }) => open ? 'translateX(100%)' : 'translateX(0)'};
      opacity: ${({ open }) => open ? 0 : 1};
    }
    &:nth-child(3) {
      transform: ${({ open }) => open ? 'rotate(-45deg)' : 'rotate(0)'};
    }
  }
`;


const Ul = styled.ul`
  list-style: none;
  display: flex;
  flex-flow: row nowrap;
  text-align: center;
  li {
    margin-top: auto;
    margin-bottom: auto;
  }
  @media (max-width: 850px) {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    margin-top: auto;
    margin-bottom: auto;
    flex-flow: column nowrap;
    background-color: #fff;
    position: fixed;
    transform: ${({ open }) => open ? 'translateX(0)' : 'translateX(100%)'};
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    transition: transform 0.3s ease-in-out;
    li {
        color: #fff;
        padding: 5px 5px;
        margin-top: 15px;
    margin-bottom: 15px;
    }
    a {
      color: #000;
      font-weight: bold;
      font-size: larger;
      letter-spacing: 4px;
    }
  a:hover {border-bottom: 5px solid #fff;}
  }
`;


const Burger = () => {
  const [open, setOpen] = useState(false)
  
  return (
    <>
      <StyledBurger open={open} onClick={() => setOpen(!open)}>
        <div />
        <div />
        <div />
      </StyledBurger>
      <Ul open={open}>
      <li>
        <div onClick={() => setOpen(!open)}>
          <Link className="button" style={{ textDecoration: 'none'}} to="/bio">BIO</Link>
        </div>
      </li>
      <li>
        <div onClick={() => setOpen(!open)}>
          <Link className="button" style={{ textDecoration: 'none'}} to="/gallery">GALLERY </Link>
        </div>
      </li>
      <li>
        <div onClick={() => setOpen(!open)}>
          <Link className="button" style={{ textDecoration: 'none'}} to="/contact">CONTACT</Link>
        </div>
      </li>
    </Ul>
    </>
  )
}
export default Burger