import React from "react";
import ReactDOM from 'react-dom';
import Insta from './insta';





class GalleryItems extends React.Component {
  render() {
  	return (
      <div style={{ paddingTop: '65px', paddingLeft: '10px', paddingRight: '10px', display:(this.props.visible ? 'inherit' : 'none')}} >
            
            <div className="rowGallery">
            {
              this.props.imgs.map(({url, name}, i)=>
                (<div key={"gallerImage"+i} className="columnGallery" onClick={()=>this.props.onClick(i)}>
                  <div className="cardGall" >
                    <Insta url={url} classStyle="galleryInsta"/>
                    <div className="overlay">{name}</div>
                  </div>
                </div>)
                )
            }
            </div>
          </div>
          );
    }
  }


export default GalleryItems;

