import React from "react";
import ReactDOM from 'react-dom';
import Insta from './insta';
import Swiper from 'react-id-swiper';


const style = {
	closeButton:{
    padding: 10,
    width: 50,
    height: 50,
    textAlign: 'center',
    background: 'black',
    borderRadius: '50%',
    color: 'white',
    fontWeight: 'bold',
    fontSize: 23,
    position: 'absolute',
    right: 0,
    zIndex: 100,
    margin:10,
    cursor: 'pointer'
  }
}


const params ={
      slidesPerView: 1,
      spaceBetween: 30,
      loop: true,
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
        type: 'bullets'
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      }
}


class MySwiper extends React.Component {
 

  render() {
    let newParams = {...params, initialSlide:this.props.selected}

  	return (
      <div style={{ display: 'inline-flex', overflow: 'hidden', paddingTop: '55px', position: 'absolute', top: '0', right: '0', bottom: '0', left: '0'}}>
       <div onClick={()=>this.props.onClick(null)} style={style.closeButton}>X</div>
       <Swiper {...newParams}>
          {this.props.imgs.map(({url, name}, i)=>
              (
                <div key={"artimage"+i}>
                  <p className="description">{name}</p>
                  <Insta url={url} classStyle="slideShowInsta"/>
                </div>
              )
          )}
      </Swiper>
    </div>
    );
  }
}

export default MySwiper;

